<style>
  .calendar_container {
    position: relative;
  }
</style>

<template>
  <div class="calendar_container">
    <input type="text" value="{{ date_format }}" @click="showCalendar" locate="{{ location }}">
    <input type="hidden" name={{name}} value="{{ date_copy }}">
    <datepicker-calendar :date="date" :visible="isVisible" @change="selectDate" @cancel="hideCalendar" :locate="vlocate"></datepicker-calendar>
  </div>
</template>

<script>
import Moment from 'moment'
import DatepickerCalendar from './DatepickerCalendar'
import t from '../modules/translate.js'


export default {
  components: {
      'datepicker-calendar': DatepickerCalendar
  },
  props: {
    value: { type: String, required: true },
    format: { type: String, default: "YYYY-MM-DD" },
    name: { type: String },
    locate: { type: String, default: "fr" }
  },
  methods: {
    selectDate: function(date) {
      this.date = date
      this.hideCalendar()
    },
    showCalendar: function() {
      Moment.locale(this.locate)
      this.isVisible = true
      setTimeout(() => document.addEventListener('click', this.hideCalendar), 0)
    },
    hideCalendar: function() {
      this.isVisible = false
      setTimeout(() => document.removeEventListener('click', this.hideCalendar), 0)
    }
  },
  data: function() {
    Moment.locale(this.locate)
    //console.log(Moment().isoWeekday(1).format("dd"))
    return {
      isVisible: false,
      date: Moment(this.value, 'YYYY-MM-DD'),
      vlocate: this.locate,
      vlogo: this.logo
    }
  },
  computed: {
    date_format () {
      return t(this.date.format(this.format), this.locate)
    },
    date_copy () {
      return t(this.date.format('YYYY-MM-DD'), this.locate)
    },
    location () {
      return this.locate
    }
  }
}
</script>



