<style lang="scss">

    $header-height: 100px;
    $day-size: 41px;

    .calendar {
        position: absolute;
        width: 315px;
        top: 100%;
        /*height: 315px;*/
        z-index: 5;
        background-color: #fff;
        box-shadow: 0 14px 45px rgba(0,0,0,0.25), 0 10px 18px rgba(0,0,0,0.22);
        -moz-box-shadow: 0 14px 45px rgba(0,0,0,0.25), 0 10px 18px rgba(0,0,0,0.22);
        -webkit-box-shadow: 0 14px 45px rgba(0,0,0,0.25), 0 10px 18px rgba(0,0,0,0.22);
    }

    .calendar_header {
        background-color: #0B610B;
        color: white;
        padding: 13px 20px 20px 20px;
        height: $header-height;
    }

    .calendar_year {
        position: relative;
        overflow: hidden;
        height: 16px;
        opacity: 0.7;
        margin-top: 15px;
        margin-bottom: 0px;
        line-height: 16px;
    }

    .calendar_date {
        position: relative;
        overflow: hidden;
        height: 40px;
        font-size: 18px;
        line-height: 40px;
    }

    .calendar_week {
        position: relative;
        font-size: 12px;
        line-height: 12px;
        color: rgba(0,0,0,0.8);
        padding: 0 14px;
    }

    .calendar_wd {
        float: left;
        width: $day-size;
        text-align: center;
    }

    .calendar_days {
        position: relative;
        margin: 14px 14px 0;
        height: $day-size * 5;
        overflow: hidden;
        transition: height 0.3s;
        &.has-6-weeks {
            height: $day-size * 6;
        }
    }

    .calendar_day {
        position: relative;
        width: $day-size;
        height: $day-size;
        float: left;
        text-align: center;
        line-height: $day-size;
        cursor: pointer;
        -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        -moz-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        transition: all 450ms cubic-bezier(0.25, 0.20, 0.25);
    }

    .calendar_day:hover {
        color: white;
        .calendar_day_effect {
            transform: scale(1);
            opacity: 0.4;
        }
    }

    .calendar_day.selected {
        color: white;
        .calendar_day_effect {
            transform: scale(1);
            opacity: 1;
        }
    }

    .calendar_day_effect {
        position: absolute;
        top: 3px;
        left: 3px;
        width: 36px;
        height: 36px;
        border-radius: 50%;
        background-color: #088A08;
        -webkit-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        -moz-transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        transition: all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;
        transform: scale(0);
        opacity: 0;
    }

    .calendar_day_text {
        position: relative;
    }

    .calendar_controls {
        position: relative;
        height: 56px;
        line-height: 56px;
        text-align: center;

        button {
            position: relative;
            border: none;
            background-color: transparent;
            user-select: none;
            outline: none;
            cursor: pointer;
            width: 56px;
            height: 56px;
        }

        svg {
            width: 24px;
            height: 24px;
            fill: rgba(0,0,0,0.87);
            vertical-align: middle;
        }

    }

    .calendar_month {
        position: absolute;
        height: 16px;
        /*background: #888;*/
        line-height: 16px;
        overflow: hidden;
        top: 20px;
        left: 0;
        right: 0;
        bottom: 0;
    }

    .calendar_next {
        float: right;
    }

    .calendar_prev {
        float: left;
    }

    .calendar_actions {
        text-align: right;
        padding: 8px;
    }

    .calendar_actions button {
        border: none;
        background-color: transparent;
        display: inline-block;
        cursor: pointer;
        outline: none;
        color: #0B610B;
        font-size: 14px;
        text-transparent: uppercase;
        font-weight: 500;
        min-width: 80px;
        line-height: 36px;
        text-align: center;
        -webkit-appearence: none;
        -webkit-transition: all 0.3s;
        -moz-transition: all 0.3s;
        transition: all 0.3s;
        &:hover {
            background-color: rgba(153,153,153,0.2);
        }
    }

    .calendar-slide-transition {
        opacity: 1;
        transition: all 0.3s;
        transform: translateY(0);
    }

    .calendar-slide-leave, .calendar-slide-enter {
        opacity: 0;
        transform: translateY(-15px);
    }

    @import 'animations';

</style>

<template>
    <div class="calendar" v-if="visible" transition="calendar-slide" @click.stop>
        <div class="calendar_header">
            <div style="float:left">
                <img src="../assets/gdt.png" width="80px" height="80px">
            </div>
            <div style="float:right;width:180px;margin-top:5px">
                <div class="calendar_year">
                    <span v-for="year in [year]" transition="slideh" :class="dayDirection">{{year}}</span>
                </div>
                <div class="calendar_date">
                    <span v-for="date_format in [date_format]" transition="slideh" :class="dayDirection">{{date_format}}</span>
                </div>
            </div>
        </div>

        <div class="calendar_controls">
            <div class="calendar_month">
                <span v-for="month in [month]" transition="slideh" :class="classDirection">{{ t(month.getFormatted())}}</span>
            </div>
            <button class="calendar_next" @click="nextMonth()">
                <svg viewBox="0 0 24 24"><path d="M10 6L8.59 7.41 13.17 12l-4.58 4.59L10 18l6-6z"></path></svg>
            </button>
            <button class="calendar_prev" @click="prevMonth()">
                <svg viewBox="0 0 24 24"><path d="M15.41 7.41L14 6l-6 6 6 6 1.41-1.41L10.83 12z"></path></svg>
            </button>
        </div>

        <div class="calendar_week">
            <div v-for="day in days" track-by="$index" class="calendar_wd">
                {{ day }}
            </div>
        </div>
        <div class="calendar_days" :class="classWeeks">
            <div v-for="month in [month]" transition="slidev" :class="classDirection">
                <div class="calendar_day" :style="{width: (month.getWeekStart() * 41) + 'px'}">
                </div>
                <div class="calendar_day" @click="selectDate(day)" v-for="day in month.getDays()" :class="{selected: isSelected(day)}">
                    <span class="calendar_day_effect"></span>
                    <span class="calendar_day_text">{{ t(day.format('DD')) }}</span>
                </div>
            </div>
        </div>
        <!-- <div class="calendar_actions">
            <button @click="cancel">Annuler</button>
            <button @click="submit">OK</button>
        </div> -->
    </div>
</template>

<script>
import Month from '../modules/month.js'
import Moment from 'moment'
import Week from '../modules/week.js'
import t from '../modules/translate.js'

export default {
    props: {
        date: {},
        locate: {},
        visible: {type: Boolean, default: true}
    },
    data () {
        //console.log(this.locate)
        var w = new Week(this.locate)
        Moment.locale(this.locate)
        return {
            //days: ['L', 'M', 'M', 'J', 'V', 'S', 'D'],
            days: w.getWeekReturn(),
            month: new Month(this.date.month(), this.date.year()),
            classDirection: "off",
            dayDirection: "off",
            vlogo: this.logo
        }
    },
    methods: {
        t: function(day) {
          return t(day, this.locate)
        },
        isSelected: function(day) {
            Moment.locale(this.locate)
            return this.date.unix() === day.unix()
        },
        selectDate: function(day) {
            Moment.locale(this.locate)
            this.dayDirection = 'direction-next'
            if (day.isBefore(this.date))
                this.dayDirection = 'direction-prev'
            this.date = day.clone()
            this.submit()
        },
        nextMonth: function() {
            Moment.locale(this.locate)
            this.classDirection = 'direction-next'
            let month = this.month.month + 1
            let year = this.month.year
            if ( month > 11 ) {
                year += 1
                month = 0
            }
            this.month = new Month(month, year)
        },
        prevMonth: function() {
            Moment.locale(this.locate)
            this.classDirection = 'direction-prev'
            let month = this.month.month - 1
            let year = this.month.year
            if ( month < 0 ) {
                year -= 1
                month = 11
            }
            this.month = new Month(month, year)
        },
        submit: function() {
            this.$dispatch('change', this.date)
        },
        cancel: function() {
            this.$dispatch('cancel')
        }
    },
    watch: {
        visible: function(val, oldval) {
            if (val === false) {
                this.classDirection = "off"
                this.dayDirection = "off"
            }
        }
    },
    computed: {
        year () {
            return t(this.date.format('YYYY'), this.locate)
        },
        date_format () {
            return t(this.date.format('dddd DD MMM'), this.locate)
        },
        classWeeks () {
            return 'has-' + this.month.getWeeks() + "-weeks"
        }
    }
}
</script>



