var translate = function(data, locate) {
  var d = "";
  for ( var i = 0; i < data.length; i++ ) {
    switch (data.charAt(i)) {
      case '0':
        if (locate == 'km') d = d + '០';
        else d = d + '0';
        break;
      case '1':
        if (locate == 'km') d = d + '១';
        else d = d + '1';
        break;
      case '2':
        if (locate == 'km') d = d + '២';
        else d = d + '2';
        break;
      case '3':
        if (locate == 'km') d = d + '៣';
        else d = d + '3';
        break;
      case '4':
        if (locate == 'km') d = d + '៤';
        else d = d + '4';
        break;
      case '5':
        if (locate == 'km') d = d + '៥';
        else d = d + '5';
        break;
      case '6':
        if (locate == 'km') d = d + '៦';
        else d = d + '6';
        break;
      case '7':
        if (locate == 'km') d = d + '៧';
        else d = d + '7';
        break;
      case '8':
        if (locate == 'km') d = d + '៨';
        else d = d + '8';
        break;
      case '9':
        if (locate == 'km') d = d + '៩';
        else d = d + '9';
        break;
      default:
        d = d + data.charAt(i);
        break;
    }
  }
  return d;
};

module.exports =  translate;



