import Moment from 'moment'

export default class Week {
  constructor(lieu) {
    this.lieu = lieu
  }

  getWeekReturn() {
    switch (this.lieu) {
      case 'km':
        return ['ច', 'អ', 'ពុ', 'ព្រ', 'សុ', 'សៅ', 'អា'];
      case 'fr':
        return ['L', 'M', 'M', 'J', 'V', 'S', 'D'];
      //case 'en':
      //  return ['M', 'T', 'W', 'T', 'F', 'S', 'S'];
      default:
        return [Moment().isoWeekday(7).format("dd"), Moment().isoWeekday(1).format("dd"), Moment().isoWeekday(2).format("dd"), Moment().isoWeekday(3).format("dd"), Moment().isoWeekday(4).format("dd"), Moment().isoWeekday(5).format("dd"), Moment().isoWeekday(6).format("dd")];
    }
  }

}



